@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
a {
  text-decoration: none;
  color: black;
}
.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 100%;
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
}
.header div {
  width:33%;
  text-align: center;
  padding: 10px;
}
.header div:hover {
  background-color: #3285fb;
  color:  white;  
}
@media only screen and (max-width: 600px) {
  .header {
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    font-size: 20px;
    margin-bottom: 20px;
  }
  .header div{
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    width: 100%;
    font-size: 20px;
  }
}
div.socials {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
div.socials div{
  margin:10px;
}
div#info {
  margin: 0 auto;
  text-align: center;
  width: 40%;
  height: 10%;
  background-color: whitesmoke;
  padding: 15px;
}

div.social-login {
  width: 60%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
div.social-login img {
  width: 60px;
  height: 60px;
  color: white;
}
div.social-login a {
  color: white;
}
.login-page {
  width: 360px;
  padding: 2% 0 0;
  margin: auto;
}
.form {
  position: relative;
  z-index: 1;
  background: #ffffff;
  max-width: 360px;
  margin: 0 auto 50px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #3285fb;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button:hover,
.form button:active,
.form button:focus {
  background: #3285fb;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form .message a {
  color: #3285fb;
  text-decoration: none;
}
.form .register-form {
  display: none;
}
.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before,
.container:after {
  content: "";
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #ef3b3a;
}
body {
  background: lightblue; /* fallback for old browsers */
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.kep-login-github {
  font-family: Helvetica, sans-serif;
  font-weight: 700;
  -webkit-font-smoothing: antialiased;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: calc(0.3vw + 12.71074px);
  text-decoration: none;
  text-transform: uppercase;
  background-color: rgb(71, 71, 71);
  border: calc(0.06887vw + 0.67769px) solid rgb(71, 71, 71);
  border-left: calc(0.6vw + 0.67769px) solid rgb(71, 71, 71);
  border-right: calc(0.6vw + 0.67769px) solid rgb(71, 71, 71);
  
  padding: calc(0.4vw + 13.38843px) calc(0.34435vw + 18.38843px);
}
.kep-login-github.small {
  padding: calc(0.34435vw + 3.38843px) calc(0.34435vw + 8.38843px);
}
.kep-login-github.medium {
  padding: calc(0.34435vw + 8.38843px) calc(0.34435vw + 13.38843px);
}
.kep-login-github.metro {
  border-radius: 0;
}
.kep-login-github .fa {
  margin-right: calc(0.34435vw + 3.38843px);
}

/* Design inspiration:  https://dribbble.com/shots/1978243-Latest-News */
