  transition: .25s; }

.card-header {
  height: 150px;
  width: 100%;
  padding: 15px;
  width: 100%;
  background-size: cover;
  color: #fff; }

.card-header--title {
  text-transform: uppercase;
  margin: 0; }

.card-body {
  padding: 15px;
  background-color: #fff;
  width: 100%; }

.date {
  font-size: 11px;
